/* General Reset */
.nav-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

/* Header layout */
header {
  position: sticky;
  top: 0;
  background-color: #fff;
  z-index: 1000;
  padding: 1rem 2rem;
  border-bottom: 1px solid #ddd;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Logo */
header img {
  height: 60px;
}

/* Nav links for desktop */
.nav-links {
  display: flex;
  gap: 1.5rem;
  align-items: center;
}

/* Each nav button */
.nav-btn img {
  height: 24px;
  width: 24px;
}

/* Cart button specifically */
.cart-btn {
  position: relative;
}

/* Cart item count */
.cart-count {
  position: absolute;
  top: -6px;
  right: -10px;
  background-color: black;
  color: white;
  font-size: 0.7rem;
  padding: 2px 6px;
  border-radius: 999px;
  font-weight: bold;
}

/* Hamburger icon button */
.hamburger {
  background: none;
  border: none;
  font-size: 1.8rem;
  cursor: pointer;
  display: none;
  z-index: 2001;
}

/* Mobile dropdown */
@media (max-width: 768px) {
  .nav-links {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 100%;
    right: 1rem;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 14px rgba(0, 0, 0, 0.1);
    padding: 1rem;
    gap: 1rem;
    min-width: 180px;
    z-index: 2000;
    transition: all 0.3s ease;
  }

  .nav-links.open {
    display: flex;
  }

  .hamburger {
    display: block;
  }

  .nav-links a,
  .nav-links button {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    text-decoration: none;
    color: black;
    background: transparent;
    border: none;
    font-size: 1rem;
    padding: 0.5rem;
    cursor: pointer;
    transition: background-color 0.2s ease;
  }

  .nav-links a:hover,
  .nav-links button:hover {
    background-color: #f5f5f5;
    border-radius: 4px;
  }
}